<template>
  <div class="select-tags">
    <input id="tags" v-model="tags" name="tags" type="hidden" />
    <div class="tags-selected">
      <span v-for="tag in tags" :key="tag" class="tag-item">
        <span class="text"
          >{{ tag
          }}<i
            :data-name="tag"
            class="iconfont icon-close"
            @click="clickRemoveTag"
        /></span>
      </span>
    </div>
    <input
      ref="tagInput"
      v-model="inputTag"
      :placeholder="'Ê†áÁ≠æÔºàÁî®ÈÄóÂè∑ÂàÜÈöîÔºåÊúÄÂ§ö' + maxTagCount + '‰∏™Ôºâ'"
      class="input"
      type="text"
      @input="autocomplete"
      @keydown.delete="removeTag"
      @keydown.enter="addTag"
      @keydown.32="addTag"
      @keydown.186="addTag"
      @keydown.188="addTag"
      @keydown.38="selectUp"
      @keydown.40="selectDown"
      @keydown.esc="close"
      @focus="openRecommendTags"
      @blur="closeRecommendTags"
      @click="openRecommendTags"
    />
    <div v-if="autocompleteTags.length > 0" class="autocomplete-tags">
      <div class="tags-container">
        <section class="tag-section">
          <div
            v-for="(item, index) in autocompleteTags"
            :key="item"
            :class="{ active: index === selectIndex }"
            class="tag-item"
            @click="selectTag(index)"
            v-text="item"
          />
        </section>
      </div>
    </div>
    <div v-if="showRecommendTags" class="recommend-tags">
      <div class="tags-container">
        <div class="header">
          <span>Êé®ËçêÊ†áÁ≠æ</span>
          <span class="close-recommend"
            ><i class="iconfont icon-close" @click="closeRecommendTags"
          /></span>
        </div>
        <a
          v-for="tag in recommendTags"
          :key="tag"
          class="tag-item"
          @click="addRecommendTag(tag)"
          v-text="tag"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Array,
      default() {
        return []
      }
    },
    suggestTags: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      tags: this.value || [],
      maxTagCount: 3, // ÊúÄÂ§öÂèØ‰ª•ÈÄâÊã©ÁöÑÊ†áÁ≠æÊï∞Èáè
      maxWordCount: 5, // ÊØè‰∏™Ê†áÁ≠æÊúÄÂ§ßÂ≠óÊï∞
      showRecommendTags: false, // ÊòØÂê¶ÊòæÁ§∫Êé®Ëçê
      inputTag: '',
      autocompleteTags: [],
      selectIndex: -1
    }
  },
  computed: {
    // Êé®ËçêÊ†áÁ≠æ
    recommendTags() {
      return this.suggestTags
      // return this.$store.state.config.config.recommendTags
    }
  },
  methods: {
    removeTag(event, tag) {
      const selectionStart = this.$refs.tagInput.selectionStart
      if (!this.inputTag || selectionStart === 0) {
        // inputÊ°ÜÊ≤°ÂÜÖÂÆπÔºåÊàñËÄÖÂÖâÊ†áÂú®È¶ñ‰ΩçÁöÑÊó∂ÂÄôÂ∞±Âà†Èô§ÊúÄÂêé‰∏Ä‰∏™Ê†áÁ≠æ
        this.tags.splice(this.tags.length - 1, 1)
        this.$emit('input', this.tags)
      }
    },

    clickRemoveTag(event) {
      const tag = event.target.dataset.name
      if (tag) {
        const index = this.tags.indexOf(tag)
        if (index !== -1) {
          this.tags.splice(index, 1)
          this.$emit('input', this.tags)
        }
      }
    },

    /**
     * ÊâãÂä®ÁÇπÂáªÈÄâÊã©Ê†áÁ≠æ
     * @param index
     */
    selectTag(index) {
      this.selectIndex = index
      this.addTag()
    },

    /**
     * Ê∑ªÂä†Ê†áÁ≠æ
     * @param event
     */
    addTag(event) {
      if (event) {
        event.stopPropagation()
        event.preventDefault()
      }

      if (
        this.selectIndex >= 0 &&
        this.autocompleteTags.length > this.selectIndex
      ) {
        this.addTagName(this.autocompleteTags[this.selectIndex])
      } else {
        this.addTagName(this.inputTag)
      }
      this.autocompleteTags = []
      this.selectIndex = -1
    },

    /**
     * Ê∑ªÂä†Êé®ËçêÊ†áÁ≠æ
     * @param tagName
     */
    addRecommendTag(tagName) {
      this.addTagName(tagName)
      this.closeRecommendTags()
    },

    /**
     * Ê∑ªÂä†Ê†áÁ≠æ
     * @param tagName Ê†áÁ≠æÂêçÁß∞
     * @returns {boolean} ÊòØÂê¶ÊàêÂäü
     */
    addTagName(tagName) {
      if (!tagName) {
        return false
      }

      // ÊúÄÂ§öÂõõ‰∏™Ê†áÁ≠æ
      if (this.tags && this.tags.length >= this.maxTagCount) {
        return false
      }

      // ÊØè‰∏™Ê†áÁ≠æÊúÄÂ§ö15‰∏™Â≠óÁ¨¶
      if (tagName.length > this.maxWordCount) {
        return false
      }

      // Ê†áÁ≠æÂ∑≤ÁªèÂ≠òÂú®
      if (this.tags && this.tags.includes(tagName)) {
        return false
      }

      this.tags.push(tagName)
      this.inputTag = ''
      this.$emit('input', this.tags)
      return true
    },

    async autocomplete() {
      this.closeRecommendTags()
      this.selectIndex = -1
      if (!this.inputTag) {
        this.autocompleteTags = []
      } else {
        const ret = await this.$axios.post('/api/tag/autocomplete', {
          input: this.inputTag
        })
        this.autocompleteTags = []
        if (ret.length > 0) {
          for (let i = 0; i < ret.length; i++) {
            this.autocompleteTags.push(ret[i].name)
          }
        }
      }
    },

    selectUp(event) {
      event.stopPropagation()
      event.preventDefault()
      const curIndex = this.selectIndex
      const maxIndex = this.autocompleteTags.length - 1
      if (maxIndex < 0 || curIndex < 0) {
        // Â∑≤ÁªèÂú®ÊúÄÈ°∂ÈÉ®
        return
      }
      this.selectIndex--
    },

    selectDown(event) {
      event.stopPropagation()
      event.preventDefault()
      const curIndex = this.selectIndex
      const maxIndex = this.autocompleteTags.length - 1
      if (maxIndex < 0 || curIndex >= maxIndex) {
        // Â∑≤ÁªèÂú®ÊúÄÂ∫ïÈÉ®
        return
      }
      this.selectIndex++
    },

    // ÂÖ≥Èó≠Êé®Ëçê
    openRecommendTags() {
      this.showRecommendTags = true
    },

    // ÂºÄÂêØÊé®Ëçê
    closeRecommendTags() {
      setTimeout(() => {
        this.showRecommendTags = false
      }, 300)
    },

    // ÂÖ≥Èó≠Ëá™Âä®Ë°•ÂÖ®
    close() {
      if (this.autocompleteTags && this.autocompleteTags.length > 0) {
        this.autocompleteTags = []
        this.selectIndex = -1
      }
      this.closeRecommendTags()
    }
  }
}
</script>

